import React, { useState } from 'react';
import './App.css';
import 'bootstrap/dist/css/bootstrap.min.css';
import {
    Container, 
    Row,
    Col, 
    Button 
} from 'react-bootstrap';
import Question from './component/Question';
import sortSongArray from './utils/randomSongs'

const App = () => {
  const [isStarted, setIsStarted] = useState(false);
  const [songIndex, setSongIndex] = useState(0);
  const [forceUpdateAt, setForceUpdateAt] = useState(Date.now());
  const [reveal, setReveal] = useState(false);

  return (
    <div>
      <Row className="justify-content-md-center">
        <Col md="12">
          <h1 className="top">猜歌遊戲</h1>
        </Col>
      </Row>
      <Row className="justify-content-md-center">
        <Col md="8">
          { !isStarted && (
            <Button 
              className="main-center"
              size="lg" 
              variant="primary" 
              onClick={e => setIsStarted(true)}
            >
              開始遊戲
            </Button>
            )
          }
          {
            (isStarted && sortSongArray[songIndex]) && <div>
              歌曲-{`${songIndex + 1}`}
              <Question
                forceUpdateAt={forceUpdateAt}
                name={sortSongArray[songIndex]['name']}
                urlPath={sortSongArray[songIndex]['urlPath']}
                revealed={reveal}
              />

              <Button 
                className="center"
                size="lg" 
                variant="primary" 
                onClick={e => {setReveal(false); setForceUpdateAt(Date.now())}}
              >
                重新
              </Button>
              <Button 
                className="center"
                size="lg" 
                variant="danger" 
                onClick={e => {setReveal(true)}}
              >
                看答案
              </Button>
              <Button 
                className="center"
                size="lg" 
                variant="success" 
                onClick={e => {setReveal(false); setSongIndex(songIndex + 1)}}
              >
                下一題
              </Button>
            </div>
          }
          {
            (isStarted && !sortSongArray[songIndex]) && <div>
              {/* <img width='100%' src='https://media.giphy.com/media/JltOMwYmi0VrO/giphy.gif' /> */}
              <Button 
                className="main-center margin-top"
                size="lg" 
                variant="primary" 
                onClick={e => setSongIndex(0)}
              >
                再玩一次
              </Button>
            </div>
          }
        </Col>
      </Row>
    </div>
  )
}

const index = () => {
  return (
    <div>
      <Container>
        <App />
      </Container>
    </div>

  )
}

export default index;
