import React from 'react';

const Question = (props) => {
  const {name, urlPath, forceUpdateAt, revealed} = props;
  let urlPathReal = urlPath;
  let showName = "";
  let revealedClass = "";
  if (revealed) {
    urlPathReal = urlPath.substring(0, urlPath.length - 7);
    showName = `歌名：${name}`;
    revealedClass = "revealed";
  }

  return (
    <div>
    <h3>{`${showName}`}</h3>
    <div class="youtube-container">
      <iframe
        class={`${revealedClass}`}
        title="songsVideo" 
        width="100%" 
        height="auto" 
        src={`${urlPathReal}&forceUpdateAt=${forceUpdateAt}`}  
        frameBorder="0" 
        allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" 
        allowFullScreen
      ></iframe>
  </div>
  </div>
  )
}

export default Question;
